        <AccordionItem>
          <AccordionItemHeading>
            <AccordionItemButton>
              Submarine Fleet Ship Classes
            </AccordionItemButton>
          </AccordionItemHeading>
          <AccordionItemPanel>
            <p>
              The Submarine Fleet is a support fleet that has 3 positions
              occupied by Submarines (SSs)
            </p>
            <hr />
            <h3>Submarines and Aviation Submarines (SS/SSV)</h3>
            <img
              rel="noopener noreferrer"
              target="_blank"
              alt="..."
              src={require("../../assets/img/others/shiphull/ss.png")}
            />
            <img
              rel="noopener noreferrer"
              target="_blank"
              alt="..."
              src={require("../../assets/img/others/shiphull/ssv.png")}
            />
            <p>
              Submarines function as support-type ships and can’t be controlled by
              players. They have a limited hunting range, shown by a red grid on the
              stage map. The submarine in the middle position determines the hunting
              range. You can move the sub fleet, which moves the hunting range, but
              it costs oil. Submarines equip two Submarine Torpedoes, and usually
              equip a DD gun as well.
            </p>
            <p>
              The only Aviation Submarine at the time of writing in the game is I-13.
              She can be equipped with a Sea Plane instead of a DD gun.
            </p>
            <p>
              Submarines have 2 different modes and function differently depending on
              which one you choose:
            </p>
            <FaqUl>
              <li>
                <p>Attack Mode:</p>
                <ul>
                  <li>
                    <p>They move 2 spaces every time you move your fleet.</p>
                  </li>
                  <li>
                    <p>
                      If an enemy fleet is present within their hunting range, they’ll attack
                      it after you move your fleet and reduce the HP of enemies in that fleet
                      by a certain percentage.
                    </p>
                  </li>
                  <li>
                    <p>
                      “Damage” dealt depends on your submarine fleets levels and fleet power.
                      Each attack consumes 1 ammo.
                    </p>
                  </li>
                </ul>
              </li>
              <li>
                <p>Support Mode:</p>
                <ul>
                  <li>
                    <p>They no longer move automatically and must be called into battle to do damage.</p>
                  </li>
                </ul>
              </li>
            </FaqUl>
            <p>Submarines can be called into battle against an enemy fleet (using the submarine button):</p>
            <ul>
              <li>
                <p>They can only be called in from within their hunting range.</p>
              </li>
              <li>
                <p>Calling them consumes oil and 1 ammo.</p>
              </li>
              <li>
                <p>
                  Submarines will attack enemies until they retreat after a set amount of
                  time. This depends on the amount of Oxygen stat they have
                  (affecting their sortie time as a submerged unit), and a set time that
                  they’ll remain in battle after they surface (determined by which sub
                  you use). When surfaced, subs become vulnerable to attacks.
                </p>
              </li>
              <li>
                <p>
                  They cannot be called into battle if all of them sink, or if the submarine fleet runs out of ammo.
                </p>
              </li>
            </ul>
            <p>
              A submarine will always do the most damage when called into battle by
              landing as many torpedoes as possible. This is why the Snorkel
              auxiliary gear is so highly recommended, as it usually extends the
              battle time of a submarine enough to let them launch another salvo of
              torpedoes. However, on submarines that launch the same number of
              torpedoes whether they have a Snorkel or not, it’s better to equip a
              rainbow Oxygen Torpedo aux instead so that they do more damage per
              torpedo. The Pressure-Resistant Hull is the go-to for a submarine’s
              second auxiliary, as it moves submarines forward and helps them reach
              their full damage potential (submarines on the top and bottom sometimes
              fail to fire because enemies aren’t within range; hull fixes this
              issue). Hull also gives more accuracy and consistent damage for your
              flag sub depending on the types of enemies faced, though your flag
              submarine can also equip an oxytorp in the second slot for maximum
              potential damage against enemies that don’t move around too much.
              Ultimately, you may find one better than the other depending on your
              personal preference. There are also some unique subs that vary from
              these general rules for aux equips.
            </p>
          </AccordionItemPanel>
        </AccordionItem>

