            <p>
              After her retrofit Yuudachi is actually quite durable and her
              heavy pellet output with strong torps makes her one of the best
              picks for W14 vanguard duties. Her strong ASW also makes her a
              good contender as your hedgehog user in the middle position.
            </p>
