import {
  Card,
  CardBody,
  Container,
  Col,
  Nav,
  NavItem,
  NavLink,
  TabPane,
  CardHeader,
  TabContent,
} from "reactstrap";
import IndexNavbar from "components/Navbars/IndexNavbar.js";
import DarkFooter from "components/Footers/DarkFooter.js";
import ChaptersWalkthrough from "../components/NewbGuides/ChaptersWalkthrough.js";
import UnderstandingShipHulls from "../components/NewbGuides/UnderstandingShipHulls.js";
import Fleetbuilding from "../components/NewbGuides/Fleetbuilding.js";
import SubPageBackground from '../components/Common/SubPageBackground';
import ScrollToTopButton from '../components/Common/ScrollToTopButton';
import { useEffect, useState } from 'react';
// import EffectiveFarming from "../components/NewbGuides/EffectiveFarming.js";
// import PrResearchFleetTech from "../components/NewbGuides/PrResearchFleetTech.js";

export default function Newbguide() {
  const [iconPills, setIconPills] = useState("1");

  useEffect(() => {
    document.body.classList.add("index-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;

    const urlArr = window.location.href.split("/");
    if (urlArr.length > 0) {
      switch (urlArr[urlArr.length - 1]) {
        case "walkthrough":
          setIconPills("1");
          break;
        case "shiphulls":
          setIconPills("2");
          break;
        case "fleetbuilding":
          setIconPills("3");
          break;
        case "farming":
          setIconPills("4");
          break;
        /*
        case "lab":
          setIconPills("5");
          break;
          */
        default:
          setIconPills("1");
      }
    }

    return function cleanup() {
      document.body.classList.remove("index-page");
      document.body.classList.remove("sidebar-collapse");
    };
    // eslint-disable-next-line
  }, []);

  return (
    <>
      <SubPageBackground>
        <ScrollToTopButton></ScrollToTopButton>

        <IndexNavbar isStatic={true} />
        <Container style={{ color: "#000" }}>
          <Col>
            <Card className="text-center ectl-primary">
              <CardHeader>
                <br />
                <h2>New Player's Guide</h2>
                <Nav className="justify-content-center" role="tablist" tabs>
                  <NavItem>
                    <NavLink
                      className={(iconPills === "1" ? "active" : "") + " ectl-nav-pill"}
                      rel="noopener noreferrer"
                      href="#/newbguide/walkthrough"
                      onClick={(e) => {
                        setIconPills("1");
                      }}
                    >
                      Chapters Walkthrough
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={(iconPills === "2" ? "active" : "") + " ectl-nav-pill"}
                      rel="noopener noreferrer"
                      href="#/newbguide/shiphulls"
                      onClick={(e) => {
                        setIconPills("2");
                      }}
                    >
                      Understanding Ship Hulls
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={(iconPills === "3" ? "active" : "") + " ectl-nav-pill"}
                      rel="noopener noreferrer"
                      href="#/newbguide/fleetbuilding"
                      onClick={(e) => {
                        setIconPills("3");
                      }}
                    >
                      Fleetbuilding
                    </NavLink>
                  </NavItem>
                  {/*
                  <NavItem>
                    <NavLink
                      className={(iconPills === "4" ? "active" : "") + " ectl-nav-pill"}
                      rel="noopener noreferrer"
                      href="#newbguide/farming"
                      onClick={(e) => {
                        setIconPills("4");
                      }}
                    >
                      Effective Farming
                    </NavLink>
                  </NavItem>
                  <NavItem>
                    <NavLink
                      className={(iconPills === "5" ? "active" : "") + " ectl-nav-pill"}
                      rel="noopener noreferrer"
                      href="#newbguide/lab"
                      onClick={(e) => {
                        setIconPills("5");
                      }}
                    >
                      Shipyard, Research & Fleet Tech
                    </NavLink>
                  </NavItem>
                  */}
                </Nav>
              </CardHeader>
              <CardBody>
                <hr />
                <TabContent
                  className="text-left"
                  activeTab={"iconPills" + iconPills}
                >
                  <TabPane tabId="iconPills1">
                    <ChaptersWalkthrough setIconPills={setIconPills}/>
                  </TabPane>
                </TabContent>
                <TabContent
                  className="text-left"
                  activeTab={"iconPills" + iconPills}
                >
                  <TabPane tabId="iconPills2">
                    <UnderstandingShipHulls />
                  </TabPane>
                </TabContent>
                <TabContent
                  className="text-left"
                  activeTab={"iconPills" + iconPills}
                >
                  <TabPane tabId="iconPills3">
                    <Fleetbuilding />
                  </TabPane>
                </TabContent>
                {/*
                <TabContent
                  className="text-left"
                  activeTab={"iconPills" + iconPills}
                >
                  <TabPane tabId="iconPills4">
                    <EffectiveFarming />
                  </TabPane>
                </TabContent>
                <TabContent
                  className="text-left"
                  activeTab={"iconPills" + iconPills}
                >
                  <TabPane tabId="iconPills5">
                    <PrResearchFleetTech />
                  </TabPane>
                </TabContent>
                */}
              </CardBody>
            </Card>
          </Col>
        </Container>
      </SubPageBackground>
      <DarkFooter />
    </>
  );
};